raul-timeline {
  display: block;
  font-size: 0.875rem;
}

raul-timeline .r-timeline {
  padding: 0.5rem;
}

raul-timeline .r-timeline__title {
  font-size: 1rem;
  font-weight: 700;
  color: #37474F;
  margin-bottom: 1.5rem;
}

raul-timeline .r-timeline__event {
  display: flex;
}

raul-timeline .r-timeline__event--error .r-timeline__marker {
  background-color: #D01A1F;
}

raul-timeline .r-timeline__event--warning .r-timeline__marker {
  background-color: #FEC12D;
}

raul-timeline .r-timeline__event--success .r-timeline__marker {
  background-color: #139C3E;
}

raul-timeline .r-timeline__event:last-child .r-timeline__line {
  display: none;
}

raul-timeline .r-timeline__event .r-timeline__outer__marker::before, raul-timeline .r-timeline__event .r-timeline__outer__marker::after {
  position: absolute;
  display: block;
  width: 1px;
  background-color: #C6CCD0;
  height: 0.25rem;
  content: "";
  left: 7.5px;
}

raul-timeline .r-timeline__event:first-child .r-timeline__outer__marker::before {
  display: none;
}

raul-timeline .r-timeline__event:last-child .r-timeline__outer__marker::after {
  display: none;
}

raul-timeline .r-timeline__event .r-timeline__outer__marker::before {
  top: 0;
}

raul-timeline .r-timeline__event .r-timeline__outer__marker::after {
  bottom: 0;
}

raul-timeline .r-timeline__event--error .r-timeline__outer__marker, raul-timeline .r-timeline__event--success .r-timeline__outer__marker, raul-timeline .r-timeline__event--warning .r-timeline__outer__marker {
  position: relative;
  width: 1rem;
  height: 1rem;
  border-radius: 9999px;
  border-color: #C6CCD0;
  border-style: solid;
  border-width: 1px;
}

raul-timeline .r-timeline__event--error .r-timeline__outer__marker::before, raul-timeline .r-timeline__event--error .r-timeline__outer__marker::after, raul-timeline .r-timeline__event--success .r-timeline__outer__marker::before, raul-timeline .r-timeline__event--success .r-timeline__outer__marker::after, raul-timeline .r-timeline__event--warning .r-timeline__outer__marker::before, raul-timeline .r-timeline__event--warning .r-timeline__outer__marker::after {
  content: none;
}

raul-timeline .r-timeline__event--error .r-timeline__marker, raul-timeline .r-timeline__event--success .r-timeline__marker, raul-timeline .r-timeline__event--warning .r-timeline__marker {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

raul-timeline .r-timeline__marker__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 1.5rem;
}

raul-timeline .r-timeline__outer__marker {
  position: relative;
  padding: 0.25rem;
}

raul-timeline .r-timeline__marker {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 0.25rem;
  background-color: #C6CCD0;
  position: relative;
}

raul-timeline .r-timeline__line {
  background-color: #C6CCD0;
  width: 1px;
  flex: 1 1 0%;
}

raul-timeline .r-timeline__content {
  padding-left: 1.5rem;
  margin-bottom: 1.5rem;
  flex: 1 1 0%;
  min-width: 0;
}

raul-timeline .r-timeline__content__title {
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 0.5rem;
  margin-bottom: 0;
  font-weight: 500;
}

raul-timeline .r-timeline__content__title a {
  color: #0076CC;
}

raul-timeline .r-timeline__content__title a:hover {
  text-decoration: underline;
}

raul-timeline .r-timeline__content__sub-title {
}

raul-timeline .r-timeline__content__view-more__link {
  color: #0076CC;
  font-size: 0.75rem;
}

raul-timeline .r-timeline__content__view-more__link:hover {
  text-decoration: underline;
}

raul-timeline .r-timeline__content__timestamp {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

raul-timeline .r-timeline__event--truncate .r-timeline__content__title, raul-timeline .r-timeline__event--truncate .r-timeline__content__sub-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}